var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"ged"},[_c('h1',[_vm._v("An Adaptive Scale Sea Surface Temperature Predicting Method Based on Deep Learning With Attention\n        Mechanism")]),_c('h4',[_vm._v("Jiang Xie, Jiyuan Zhang, Jie Yu, and Lingyu Xu")]),_c('h3',[_vm._v("ABSTARCT")]),_c('el-row',{attrs:{"type":"flex","justify":"center"}},[_c('el-col',{attrs:{"span":22}},[_vm._v("\n            Sea surface temperature (SST) prediction plays an important role in ocean-related fields. It is\n            challenging due to the nonlinear temporal dynamics with changing complex factors and the inherent\n            difficulties in long-scale predictions. Conventional models often lack efficient information extraction\n            and cannot meet the requirements of long-scale predictions. Therefore, the gate recurrent unit (GRU)\n            encoder–decoder with SST codes and dynamic influence link (DIL), GRU encoder– decoder (GED), which\n            considered both the static and dynamic influence, is proposed in this letter. Each SST code, capturing\n            the static information more effectively, was computed by all hidden states of the encoder and was\n            individually associated with each predicted SST. The DIL, capturing the dynamic influence, connected the\n            SST code with the early predicted future SST for solving the long-scale dependence problem. GED was\n            tested on the Bohai Sea SST data sets and South China Sea SST data sets and compared with full-connected\n            long-short term memory (FC-LSTM) and support vector regression. The results demonstrated that GED\n            outperformed others on different prediction scales and different prediction terms (daily, weekly, and\n            monthly), especially in terms of long-scale and long-term predictions. In addition, attention\n            relationships between historical and future SSTs were f